<script>
import { StoreCart } from "./StoreCart";


export let lang;
export let Data;
export let interval;
let items = []
let has_discount = $StoreCart.has_discount;
let subtotal = $StoreCart.subtotal;
let discounts = $StoreCart.discounts;
let total = $StoreCart.total;
items = Object.values($StoreCart.items); 

</script>
<section>
    <main>
        {#each items as item}
            <div class="is-flex is-justify-content-space-between is-align-items-center item-cart">
                <h3 class="has-text-white is-flex is-align-items-center">
                    <span class="icon icon-plans is-size-1">
                        <span class="{item.icon} icon plan-icons-container"><span class="path1"></span><span class="path2"></span></span>
                    </span>
                    <span class="fw400 is-size-6 title-plan has-text-left">
                        <span class="is-size-7">
                            Plan: {item.name}
                        </span>
                        <br>
                        <span class="interval is-size-7">
                            x 
                            {#if item.interval === "year"}
                                12 {lang.intervals.month[1]}
                            {/if}
                            {#if item.interval === "month"}
                                1 {lang.intervals.month[0]}
                            {/if}
                            
                        </span>
                    </span>                                 
                </h3>
                    <p class="fw500 has-text-white is-size-6">
                        <span class="price-to-pay">
                            <span>
                                {Data.currency[0]}
                            </span>
                            <span>
                                {item.prices[item.interval].current}
                            </span>
                            <span class="is-size-6">
                                {Data.currency[1]}
                            </span>
                        </span>
                    </p>
            </div>
        {/each}
        <!-- SUBTOTALES -->
        <div>
            <hr class="separtion-line">
        </div>
        <div class="fw600 has-text-white has-text-centered">
            <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                <span class="fw600">
                    {lang.forms.paymentAnyPlan.subtotal}
                </span>
                <span class="fw600">
                    <span class="price-interval">
                        <span>
                            {Data.currency[0]}
                        </span>
                        <span>
                            {subtotal[interval]}
                        </span>
                        <span class="is-size-6">
                            {Data.currency[1]}
                        </span>
                    </span>
                </span>
            </p> 
        </div>
        {#if has_discount}
            <div class="fw600 has-text-white has-text-centered">
                <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                    <span class="fw600">
                        {lang.forms.paymentAnyPlan.discounts}
                    </span>
                    <span class="fw600">
                        <span class="price-interval">
                            <span>
                                - {Data.currency[0]}
                            </span>
                            <span>
                                {discounts[interval]}
                            </span>
                            <span class="is-size-6">
                                {Data.currency[1]}
                            </span>
                        </span>
                    </span>
                </p> 
            </div>
        {/if}
        <!-- TOTAL -->
        <div>
            <hr class="separtion-line">
        </div>
        <div class="fw600 has-text-white has-text-centered">
            <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                <span class="fw600">
                    {lang.forms.paymentAnyPlan.total}
                </span>
                <span class="fw600">
                    <span class="price-interval">
                        <span>
                            {Data.currency[0]}
                        </span>
                        <span>
                            {total[interval]}
                        </span>
                        <span class="is-size-6">
                            {Data.currency[1]}
                        </span>
                    </span>
                </span>
            </p> 
        </div>
    </main>
    <footer>

    </footer>
</section>


<style>
[class*='plan-icon-']:before{
    background: #fff;
}
.separtion-line{
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.icon-plans{
    font-size:52px;
}
.title-plan{
    margin-left: 1.25rem;
}
.icon .path1:before{
    background:var(--color-primary);
}
.icon .path2:before{
    color: #fff;
}
.item-cart{
    margin: 0.75rem auto;
}
</style>